'use client';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { LayoutDashboard, ShoppingBag, Store, Image, List, Users, LogOut } from 'lucide-react';

const sidebarItems = [
  { name: 'Overview', href: '/dashboard', icon: LayoutDashboard, roles: ['admin', 'seller'] },
  { name: 'Products', href: '/dashboard/products', icon: ShoppingBag, roles: ['admin', 'seller'] },
  { name: 'Shops', href: '/dashboard/shops', icon: Store, roles: ['admin'] },
  { name: 'Categories', href: '/dashboard/categories', icon: List, roles: ['admin'] },
  { name: 'Banners', href: '/dashboard/banners', icon: Image, roles: ['admin'] },
  { name: 'Users', href: '/dashboard/users', icon: Users, roles: ['admin'] },
];

export default function DashboardLayout({ children }: { children: React.ReactNode }) {
  const pathname = usePathname();
  // We'll need user role here, for now show all if authenticated
  
  return (
    <div className="flex min-h-screen bg-gray-100">
      {/* Sidebar */}
      <aside className="w-64 bg-white shadow-md">
        <div className="p-6">
          <h1 className="text-2xl font-bold text-blue-600">ShopNest Admin</h1>
        </div>
        <nav className="mt-6">
          {sidebarItems.map((item) => (
            <Link
              key={item.href}
              href={item.href}
              className={`flex items-center px-6 py-3 text-sm font-medium transition-colors ${
                pathname === item.href
                  ? 'bg-blue-50 text-blue-600 border-r-4 border-blue-600'
                  : 'text-gray-600 hover:bg-gray-50 hover:text-blue-600'
              }`}
            >
              <item.icon className="mr-3 h-5 w-5" />
              {item.name}
            </Link>
          ))}
        </nav>
        <div className="absolute bottom-0 w-64 p-6">
          <button className="flex items-center text-sm font-medium text-red-600 hover:text-red-800 transition-colors">
            <LogOut className="mr-3 h-5 w-5" />
            Logout
          </button>
        </div>
      </aside>

      {/* Main Content */}
      <main className="flex-1 p-8 overflow-y-auto">
        {children}
      </main>
    </div>
  );
}
